export class CardModel {
    constructor(api) {
        this.cardsData = [];
        this.api = api;
    }
    async getCardsData() {
        if (!this.cardsData.length) {
            const rawProducts = await this.api.get('/product/');
            this.cardsData = rawProducts.items;
        }
        return this.cardsData;
    }
}
